@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@200;400&display=swap);
html,
body {
	font-family: "Roboto", sans-serif;
	padding: 0;
	margin: 0;
}

#root {
	height: 100vh;
}

* {
	box-sizing: border-box;
}

.joinOuterContainer {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
	height: 100vh;
	-webkit-align-items: center;
	        align-items: center;
	background-color: #303030;
}

.joinInnerContainer {
	width: 20%;
}

.joinInput {
	border-radius: 10px;
	padding: 15px 20px;

	width: 100%;
}

.heading {
	color: white;
	font-size: 2.5em;
	padding-bottom: 10px;
	border-bottom: 2px solid white;
}

.button {
	color: #fff !important;
	text-transform: uppercase;
	text-decoration: none;
	background: #2979ff;
	padding: 20px;
	border-radius: 10px;
	display: inline-block;
	border: none;
	width: 100%;
}

.mt-20 {
	margin-top: 20px;
}

@media (max-width: 480px) {
	.joinOuterContainer {
		height: 100vh;
	}

	.joinInnerContainer {
		width: 50vw;
	}
	.button.mt-20 {
		min-height: 35px !important;
	}
}

@media (min-width: 480px) {
	.joinOuterContainer {
		height: 100vh;
	}

	.joinInnerContainer {
		width: 30vw;
	}
}

@media (min-width: 980px) {
	.joinOuterContainer {
		height: 100vh;
	}

	.joinInnerContainer {
		width: 30vw;
	}
	.button {
		min-height: 40px;
		font-size: 1.2rem;
	}
}

button:focus {
	outline: 0;
}

.infoBar {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	background: #484848;
	border-radius: 4px 4px 0 0;
	height: 60px;
	width: 100%;
}

.leftInnerContainer {
	-webkit-flex: 0.5 1;
	        flex: 0.5 1;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	margin-left: 5%;
	color: white;
}

.rightInnerContainer {
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 0.5 1;
	        flex: 0.5 1;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	margin-right: 5%;
}

.onlineIcon {
	margin-right: 5%;
}

.cross {
	margin-top: 12px;
	margin-left: 30px;
}

.button {
	background-color: #2979ff !important;
	border-radius: 3 !important;
	border: 0 !important;
	height: 3.5vh !important;
	color: white !important;
	text-transform: none !important;

	padding: 0 30px !important;
	box-shadow: 0 3px 5px 2px rgba(46, 49, 49, 1) !important;
}

@media (max-width: 420px) {
	.button {
		height: 4.5vh !important;

		text-transform: none !important;
		min-width: 50px !important;
		padding: 12px 15px !important;
		text-align: center !important;

		font-size: 14px !important;
	}
}

@media (max-width: 320px) {
	.button {
		height: 5vh !important;
		text-transform: none !important;
		min-width: 50px !important;
		padding: 10px 8px !important;
		text-align: center !important;

		font-size: 12px !important;
	}
}

.form {
	display: -webkit-flex;
	display: flex;
	width: 100vw;
	border-top: 2px solid #696969;
}

.input {
	border: none;
	border-radius: 0;
	font-family: "Fira Sans", sans-serif;
	padding: 5%;
	width: 80vw;
	height: 10vh;
	font-size: 1.2em;
	background: #484848;
	color: white;
}

.input::-webkit-input-placeholder {
	color: #c8c8c8;
}

.input:-ms-input-placeholder {
	color: #c8c8c8;
}

.input::placeholder {
	color: #c8c8c8;
}

input:-ms-input-placeholder {
	/* Internet Explorer 10-11 */
	color: #c8c8c8;
}

input::-ms-input-placeholder {
	/* Microsoft Edge */
	color: #c8c8c8;
}

input:focus,
textarea:focus,
select:focus {
	outline: none;
}

.sendButton {
	color: #fff !important;
	font-size: 1.2em;
	border-radius: 0;
	text-decoration: none;
	font-family: "Fira Sans", sans-serif;
	cursor: pointer;
	background: #2979ff;
	padding: 20px;
	display: inline-block;
	border: none;
	width: 20%;
}

@media (max-width: 480px) {
	.input {
		width: 75vw;
		height: 10vh;
	}
	.sendButton {
		width: 35%;
		height: 10vh;
	}
}

@media (max-width: 320px) {
	.input {
		width: 75vw;
		height: 10vh;
	}
	.sendButton {
		width: 25vw;
		height: 10vh;
	}
}

@media (min-width: 480px) {
	.input {
		padding: 0 45px;
		height: 10vh;
	}
	.sendButton {
		width: 20vw;
	}
}

@media (min-width: 1080px) {
	.input {
		padding: 0 45px;
		height: 10vh;
		width: 90vw;
	}
	.sendButton {
		width: 10vw;
	}
}

.messageBox {
	background: #f3f3f3;
	border-radius: 20px;
	padding: 5px 20px;
	color: white;
	display: inline-block;
	max-width: 80%;
}

.messageText {
	width: 100%;
	letter-spacing: 0;
	float: left;
	font-size: 1.1em;
	word-wrap: break-word;
}

.messageText img {
	vertical-align: middle;
}

.messageContainer {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	padding: 0 5%;
	margin-top: 3px;
}

.sentText {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	font-family: "Fira Sans", sans-serif;
	color: #828282;
	letter-spacing: 0.3px;
}

.pl-10 {
	padding-left: 10px;
}

.pr-10 {
	padding-right: 10px;
}

.justifyStart {
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
}

.justifyEnd {
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
}

.colorWhite {
	color: white;
}

.backgroundBlue {
	background: #2979ff;
}

.backgroundGray {
	background: #484848;
}

.messages {
	padding: 3% 0;
	overflow: auto;
	-webkit-flex: auto;
	        flex: auto;
}
::-webkit-scrollbar {
	display: none;
}
* {
	-ms-overflow-style: none;
}

.outerContainer {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	height: 100vh;
	background-color: #1a1a1d;
}

.container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	background: #202020;
	width: 35%;
}

@media (max-width: 480px) {
	.outerContainer {
		height: 100vh;
	}

	.container {
		width: 100vw;
		height: 100vh;
	}
}

@media (min-width: 480px) {
	.container {
		width: 100vw;
		height: 100vh;
	}
	#input {
		width: 100vw;
		max-height: 30vh;
	}
}
@media (max-width: 320px) {
	.container {
		width: 100vw;
		height: 100vh;
	}
	#input {
		width: 75vw;
		height: 34px;
	}
}

.app__wrapper {
	font-family: "Fira Sans", sans-serif;
}

